//
// Sections
// --------------------------------------------------

// Section Spacing
// -------------------------

$insets: (20px,34px,45px,50px,55px,60px,70px,80px,90px,100px,105px,130px,140px,150px,195px,205px);

@include indent-responsive(section, '', (padding-top, padding-bottom), $medias, $insets);
@include indent-responsive(section, top, padding-top, $medias, $insets);
@include indent-responsive(section, bottom, padding-bottom, $medias, $insets);

// Novi section
[data-x-mode="true"] .novi-section {
	position: relative;
	z-index: 0;

	&:before {
		content: '';
		position: absolute;
		left: 0;
		top: 0;
		right: 0;
		z-index: 10;
		height: 30px;
	}
}