/*
* Fixed Layout
*/
.rd-navbar-fixed {
  display: block;

  .rd-navbar{

  }

  // RD Navbar Brand
  .rd-navbar-brand {
    position: fixed;
    top: 2px;
    left: 64px;
    z-index: 17;
    display: block;
    overflow: hidden;
    text-align: left;
    white-space: nowrap;
    text-overflow: ellipsis;

    .brand-logo {
      img {
        max-height: 50px;
        width: auto;
        max-width: none;
      }
    }
  }

  // RD Navbar Panel
  .rd-navbar-panel {
    @include display-flex();
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    padding: ($rd-navbar-fixed-height - $rd-navbar-fixed-line-height) / 2;
    height: $rd-navbar-fixed-height;
    color: $rd-navbar-fixed-panel-color;
    z-index: 999;

    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      box-shadow: $rd-navbar-fixed-shadow;
      border-bottom: $rd-navbar-fixed-border;
      background: $rd-navbar-fixed-panel-background;
    }
  }

  // RD Navbar Toggle
  .rd-navbar-toggle {
    display: inline-block;
  }

  .rd-navbar-nav-wrap {
    @include display-flex;
    @include flex-direction(column);
    @extend %rd-navbar-transition;
    position: fixed;
    top: -56px;
    bottom: 0;
    left: 0;
    width: 270px;
    padding: 112px 0 56px;
    //height: auto;
    color: $white;
    background: $white;
    border-right: $rd-navbar-fixed-border;
    z-index: 15;
    overflow-x: hidden;
    overflow-y: auto;
    @include transform(translateX(-120%));

    &.active {
      @include transform(translateX(0));
    }

    > * {
      @include flex-shrink(0);
    }
  }

  .rd-navbar-nav-wrap-outer {
    @include order(3);
  }

  .rd-navbar-nav {
    display: block;
    height: auto;
    font-size: 16px;
    line-height: 26px;
    text-align: left;

    &:before, &:after {
      display: none;
    }

    li {
      > a {
        display: block;
        font-size: 14px;
        padding: 11px 56px 11px 16px;
        color: $rd-navbar-fixed-panel-color;
      }

      &:hover,
      &.focus,
      &.active,
      &.opened {
        > a, > a:hover {
          color: $rd-navbar-fixed-panel-item-hover-color;
          background: $rd-navbar-fixed-panel-item-hover-background;
        }
        > .rd-navbar-submenu-toggle {
          &::after {
            color: $rd-navbar-fixed-submenu-toggle-focus-color;
          }
        }
      }
    }
  }

  .rd-navbar-dropdown,
  .rd-navbar-megamenu {
    display: none;
  }

  .rd-navbar-submenu {
    position: relative;
    @extend %rd-navbar-transition;

    li > a {
      letter-spacing: .04em;
      padding-left: 30px;
    }

    .rd-navbar-dropdown li li > a,
    .rd-navbar-megamenu ul li li > a {
      padding-left: 48px;
    }

    &.opened {
      > .rd-navbar-dropdown,
      > .rd-navbar-megamenu {
        display: block;
      }
    }
  }

  // RD Navbar Megamenu
  .rd-navbar-megamenu {
    > li {
      padding-top: 15px;

      &:last-child {
        padding-bottom: 10px;
      }
    }

    .rd-megamenu-header {
      padding-left: 15px;
      padding-right: 15px;
      color: $brand-primary;
    }

    .list-marked {
      margin-top: 10px;
      > li {
        padding-left: 0;
        &:before {
          display: none;
        }
      }
    }

    // Offsets
    * + .rd-megamenu-header {
      margin-top: 15px;
    }
  }

  // RD Navbar Search
  .rd-navbar-search {
    display: block;
    padding: 13px 8px 6px;

    .form-control,
    .form-label {
      font-size: 14px;
      line-height: 1.7;
      color: $gray-darker;

      @media (min-width: $screen-lg-min) {
        font-size: 16px;
      }
    }

    .form-control {
      min-height: 52px;
      padding: 13px 45px 15px 18px;
    }

    .rd-search-results-live,
    .rd-navbar-search-toggle {
      display: none;
    }
  }

  // RD Navbar Nav
  .rd-navbar-nav {
    li {
      .rd-navbar-dropdown,
      .rd-navbar-megamenu {
        transition: opacity 0.3s, height 0.4s ease;
        opacity: 0;
        height: 0;
        overflow: hidden;
      }

      &.opened {
        > .rd-navbar-dropdown,
        > .rd-navbar-megamenu {
          opacity: 1;
          height: auto;
        }
        > .rd-navbar-submenu-toggle {
          color: $rd-navbar-fixed-submenu-toggle-focus-color;
          &::after {
            @include transform(rotate(180deg));
          }
        }
      }

    }
  }

  // RD Navbar Submenu Toggle
  .rd-navbar-submenu-toggle {
    &::after {
      content: '\f078';
      position: absolute;
      top: 25px;
      right: 0;
      margin-top: -22px;
      width: 65px;
      height: 44px;
      font: 400 9px/9px "FontAwesome";
      line-height: 42px;
      text-align: center;
      transition: 0.4s all ease;
      z-index: 2;
      cursor: pointer;
      color: $rd-navbar-fixed-submenu-toggle-color;
      will-change: transform;
    }
  }

  // Hybrid Styles
  .rd-navbar-collapse,
  .rd-navbar-search-toggle {
    position: fixed;
    top: ($rd-navbar-fixed-height - $rd-navbar-fixed-line-height) / 2;
    height: $rd-navbar-fixed-line-height;
    z-index: 1000;

    background-color: transparent;
    border: none;
    &:focus {
      outline: none;
    }
  }

  // RD Navbar shop
  .rd-navbar-shop {
    position: relative;
    z-index: 1000;
    text-align: left;
    padding-left: 16px;
    right: 0;
    @include transform(translate(0,0));

    li{
      display: block;
      & + li {
        margin-top: 10px;
      }
    }
  }


  // RD Navbar aside
  .list-links {
    padding-bottom: 20px;
  }


  // RD Navbar States
  &.active {
    .rd-navbar-nav {
      @include transform(translateX(0));
    }
  }

  &.rd-navbar--on-search {
    @media (max-width: 1199px) {
      .brand-name {
        opacity: 0;
        visibility: hidden;
        @include transform(scale(0.7));
      }
    }
  }

  &.rd-navbar--is-clone {
    display: none;

    .rd-navbar-panel {
      @include transform(translateY(-110%));
    }

    &.rd-navbar--is-stuck {
      .rd-navbar-panel {
        @include transform(translateY(0));
      }
    }
  }

  .rd-navbar-fixed--visible {
    display: block;
  }

  .rd-navbar-fixed--hidden {
    display: none;
  }

  @media (min-width: $screen-lg-min) {
    .rd-navbar-panel { 
      &:before{
        background: transparent;
        box-shadow: none;
        border-bottom: 0;
      }
    }

    .rd-navbar-nav-wrap {
      padding-top: 172px;

      .rd-navbar-brand {
        position: absolute;
        display: block !important;
        top: 128px;
        left: 14px;
      }
    }

    .rd-navbar-brand {
      display: none;
    }

    .rd-navbar-toggle {
      border-radius: 50%;
      background: $white;
      box-shadow: $shadow-area-xs;

      &.active {
        ~ .rd-navbar-brand {
          @include transform(translateX(0));
        }
      }
    }
  }
}

html.rd-navbar-fixed-linked {
  @media (min-width: $screen-lg-min) {
    .rd-navbar-default {
      margin-bottom: -56px;
    }
  }
}
