/*
* @subsection   RD Navbar
*
* @description  Describes style declarations for RD Navbar extension
*
* @author       Evgeniy Gusarov
* @link         https://ua.linkedin.com/pub/evgeniy-gusarov/8a/a40/54a
*/
@import "rd-navbar_includes/_rd-navbar-mixins.scss";

/*
* @subsection General Styles
*/

// RD Navbar Basics
%rd-navbar-transition {
  transition: .3s all cubic-bezier(0.785, 0.135, 0.150, 0.860);
}

%rd-navbar-hidden {
  display: none;
}

%rd-navbar-visible {
  display: block;
}

.rd-navbar--no-transition {
  &, * {
    transition: none !important;
  }
}

.rd-navbar-wrap {
  position: relative;
  z-index: 12;
  @extend %rd-navbar-transition;
}

.rd-navbar, .rd-navbar.rd-navbar--is-clone {
  @extend %rd-navbar-hidden;
}

.rd-navbar-fixed,
.rd-navbar-static,
.rd-navbar-fullwidth,
.rd-navbar-sidebar {
  @extend %rd-navbar-visible;
}

.rd-navbar-wrap,
.rd-navbar,
.rd-navbar-brand,
.rd-navbar-slogan,
.rd-navbar-dropdown,
.rd-navbar-megamenu,
.rd-navbar-collapse-items,
.brand-name,
.rd-navbar-nav,
.rd-navbar-panel,
.rd-navbar-search-form-input,
.rd-navbar-search-form-submit,
.rd-navbar-search-toggle,
.rd-navbar-live-search-results,
.rd-navbar-search-form {
  transition: .33s all ease-out;
}

// RD Navbar Collapse
.rd-navbar-collapse {
  &-toggle {
    @include make-toggle(
        $rd-navbar-collapse-toggle-preset,
        $rd-navbar-min-line-height,
        $rd-navbar-min-font-size,
        $rd-navbar-panel-color
    );
    display: none;
  }
}

// RD Navbar States
.rd-navbar--has-sidebar {
  body {
    padding-left: 270px;
  }
}
.rd-navbar-fixed
.rd-navbar-nav-wrap,
.rd-navbar-nav-wrap {
  .rd-navbar-brand {
    display: none;
  }
}

.rd-navbar--is-stuck {
  @include box-shadow($rd-navbar-shadow);
}

.rd-navbar.rd-navbar-fixed + .rd-navbar.rd-navbar--is-clone,
.rd-navbar.rd-navbar-sidebar + .rd-navbar.rd-navbar--is-clone {
  display: none;
}

@import "rd-navbar_includes/_rd-navbar-components.scss";

/*
* @subsection   Hybrid  Styles
*/

//=========    Static & Fullwidth & Sidebar    ===========
.rd-navbar-static,
.rd-navbar-sidebar,
.rd-navbar-fullwidth {
  // RD Navbar Search
  .rd-navbar-search {
    &-form {
      &-input {
        input {
          width: 100%;
          padding: 0 10px;
          font-size: 16px;
        }
      }
    }
  }
}

//=============    Static & Fullwidth     ================
.rd-navbar-static,
.rd-navbar-fullwidth {

  address {
    line-height: 16px;
  }

  &:after {
    content: '';
    background: $rd-navbar-background;
  }

  .rd-navbar-nav > li > a,
  .rd-navbar-search-toggle {
    position: relative;
    z-index: 2;
  }

  .rd-navbar-nav {
    li a {
      letter-spacing: 0.06em;
    }
  }

  // RD Navbar Submenu
  .rd-navbar-nav {
    @include display-flex;
    @include align-items(stretch);
    > li {
      @include display-flex;
      @include align-items(center);
      @include flex-wrap(wrap);
      @media (min-width: $screen-lg-min) {
        padding-left: 20px;
        padding-right: 20px;
      }
      > a {
        position: relative;
        display: block;
        text-transform: uppercase;
        font-weight: 700;
        color: $rd-navbar-nav-color;
        background: $rd-navbar-nav-background;
        vertical-align: middle;
        transition: 300ms;
      }

      &.focus > a,
      > a:hover {
        color: $rd-navbar-nav-hover-background;
      }

      &.active > a {
        color: $rd-navbar-nav-hover-background;
      }
    }

    > .rd-navbar-submenu {
      > .rd-navbar-dropdown,
      > .rd-navbar-megamenu {
        opacity: 0;
        visibility: hidden;
        @include transform(translateY(20px));
      }

      .rd-navbar-dropdown,
      .rd-navbar-megamenu {
        @include box-shadow($rd-navbar-shadow);
      }

      &.opened,
      &.focus {
        > .rd-navbar-dropdown,
        > .rd-navbar-megamenu {
          opacity: 1;
          visibility: visible;
          @include transform(translateY(0));
        }
      }

      .rd-navbar-submenu {
        > .rd-navbar-dropdown {
          opacity: 0;
          visibility: hidden;
          @include transform(translateX(-20px));
        }

        &.focus {
          > .rd-navbar-dropdown {
            opacity: 1;
            visibility: visible;
            @include transform(translateX(0));
          }
        }
      }

      .rd-navbar-submenu.focus > .rd-navbar-dropdown,
      .rd-navbar-submenu.opened > .rd-navbar-dropdown {
        display: block;
      }
    }
  }

  // RD Navbar Inner
  .rd-navbar-inner {
    position: relative;
    max-width: $rd-navbar-width;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
  }

  // RD Navbar Nav
  .rd-navbar-nav {
    > li > .rd-navbar-dropdown {
      position: absolute;
      left: 0;
      display: block;
      margin-top: 15px;
      margin-left: -31px;
      text-align: left;
      background: $rd-navbar-dropdown-background;
      z-index: 10;
      @media (min-width: $screen-lg-min) {
        margin-top: 29px;
      }
    }

    li.rd-navbar--has-dropdown {
      position: relative;
    }

    > li.rd-navbar-submenu {
      > .rd-navbar-dropdown {
        @include order(3);
      }

      .rd-navbar-dropdown,
      .rd-navbar-megamenu {
        box-shadow: $shadow-area-ambient;
        a {
          font-size: 14px;
          text-transform: uppercase;
          font-weight: 700;
        }
      }
    }

    > li {
      > .rd-navbar-dropdown {
        top: 100%;
      }
    }
  }

  .rd-navbar-dropdown,
  .rd-navbar-megamenu .list-marked {
    li {
      padding-left: 0;
      transition: .44s all ease;
      padding-top: 3px;
      padding-bottom: 3px;
      @media (min-width: $screen-lg-min) and (min-height: 750px) {
        padding-top: 13px;
        padding-bottom: 13px;
      }

      &:before {
        display: none;
      }

      a:before {
        position: absolute;
        top: 50%;
        @include transform(translateY(-55%));
        left: -5px;
        content: '\f105';
        font: 400 14px 'FontAwesome';
        color: $burnt-sienna;
        opacity: 0;
        visibility: hidden;
        transition: .33s all ease;
        transition-delay: .1s;

        @media (min-width: $screen-lg-min) and (min-height: 750px) {
          font-size: 16px;
          //top: 16px;
        }
      }

      a {
        display: block;
      }
    }

    li:hover {
      > a {
        padding-left: 16px;
      }

      > a:before {
        left: 0;
        opacity: 1;
        visibility: visible;
      }
    }

    > li > a {
      transition: 250ms;
      background: $rd-navbar-dropdown-item-background;
      &, &:focus, &:active {
        color: $rd-navbar-color;
      }
    }

    > li > a:hover {
      color: $burnt-sienna;
      background: $rd-navbar-dropdown-item-hover-background;
    }

    > li.focus > a,
    > li.opened > a {
      color: $burnt-sienna;
      background: $rd-navbar-dropdown-item-active-background;
    }
  }

  // RD Navbar Dropdown
  .rd-navbar-dropdown {
    width: calc(100% + 40px);
    min-width: $rd-navbar-dropdown-width;
    background: $rd-navbar-dropdown-background;
    transition: .44s all ease;

    .rd-navbar-dropdown {
      position: absolute;
      top: -19px;
      left: 100%;
      margin-left: 0;
    }

    > li {
      min-width: $rd-navbar-dropdown-width;
      padding: 4px 10px 4px 30px;
      border-bottom: 1px solid $silver-light;

      @media (min-width: $screen-md-min) and (max-width: 1199px) {
        padding-top: 10px;
        padding-bottom: 10px;
      }

      &:last-of-type {
        border-bottom: 0;
      }
    }

    > li > a {
      position: relative;
      display: block;

      width: 100%;
      @media (min-width: $screen-lg-min) and (min-height: 750px) {
        padding-top: 13px;
        padding-bottom: 13px;
      }
    }
  }

  // RD Navbar Megamenu
  .rd-navbar-megamenu {
    position: absolute;
    left: 0;
    z-index: 100;
    display: table;
    table-layout: fixed;
    width: 100%;
    text-align: left;
    background: $rd-navbar-megamenu-background;
    top: 100%;
    margin-top: -10px;


      .rd-megamenu-header {
        color: $brand-primary;
        letter-spacing: 0.06em;
      }

    @media (min-width: $screen-xl-min) {
      left: auto;
      right: 0;
      width: 100%;
    }

    > li {
      position: relative;
      display: table-cell;
      padding: 23px 27px 20px 27px;

      @media (min-width: $screen-lg-min) {
        padding: 31px 27px 25px 47px;
      }

      .list-marked {
        display: inline-block;
        margin-top: 12px;
        li + li {
          margin-top: 0;
        }
      }

      * + h6,
      * + .h6 {
        margin-top: 47px;
      }

      & + li {
        position: relative;
        &:before{
          position: absolute;
          left: 0;
          top: 25px;
          bottom: 25px;
          border-left: 1px solid $gray-lighter;
          content: "";
          display: inline-block;
        }
      }
    }
  }

  // RD Navbar Submenu Toggle
  .rd-navbar-submenu-toggle {
    cursor: pointer;
    z-index: 10;
    transition: .33s all ease;
    &::after {
      content: '\f078';
      position: relative;
      display: inline-block;
      font: 400 9px "FontAwesome";
      text-align: center;
      transition: 0.4s all ease;
      z-index: 2;
      color: $rd-navbar-nav-color;
      will-change: transform;
      -webkit-filter: blur(0);
    }

    &:hover {
      color: $rd-navbar-nav-hover-background;
    }
  }

  .rd-navbar-nav > li {
    > .rd-navbar-submenu-toggle {
      position: relative;
      top: 1px;
      right: -2px;
      height: 13px;
      width: auto;
      text-align: center;
      vertical-align: bottom;
      will-change: transform;
    }

    &.focus > .rd-navbar-submenu-toggle,
    &.opened > .rd-navbar-submenu-toggle,
    & > a:hover + .rd-navbar-submenu-toggle {
      top: 2px;

      &::after {
        @include transform (rotate(-180deg));
        color: $rd-navbar-nav-hover-background;
      }
    }

    &.active > .rd-navbar-submenu-toggle,
    & > a:hover + .rd-navbar-submenu-toggle {
      &::after {
        color: $rd-navbar-nav-hover-background;
      }
    }
  }

  .rd-navbar-dropdown {
    .rd-navbar-submenu-toggle {
      display: none;
      position: absolute;
      top: 50%;
      @include transform(translateY(-50%));
      right: 40px;
      height: 32px;
      vertical-align: middle;
      margin-left: -25px;

      &::after {
        top: 1px;
      }
    }

    > li.focus > .rd-navbar-submenu-toggle,
    > li.opened > .rd-navbar-submenu-toggle,
    > li > a:hover + .rd-navbar-submenu-toggle {
      &::after {
        color: $rd-navbar-dropdown-item-hover-color;
      }
    }
  }

  // RD Navbar Clone
  &.rd-navbar--is-clone {
    display: none;

    &.rd-navbar--is-stuck {
      display: block;
    }
  }

  // RD Navbar Stuck
  &.rd-navbar--is-stuck,
  &.rd-navbar--is-clone {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 9999;
    background: $white;

    .rd-navbar-megamenu {
      top: 69px;
      margin-top: 0;
    }

    .rd-navbar-inner {
      padding-top: 12px;
      padding-bottom: 12px;
    }
  }

  .rd-navbar-megamenu {
    position: absolute;
    text-align: left;
    visibility: hidden;
    opacity: 0;
  }

  // RD Navbar States
  .rd-navbar--has-dropdown {
    position: relative;
  }

  &.rd-navbar--is-clone {
  }
}

//=============      Fixed & Sidebar      ================
.rd-navbar-fixed,
.rd-navbar-sidebar {

  // RD Navbar Collapse
  .rd-navbar-collapse-toggle {
    display: inline-block;
    z-index: 9999;
  }

  // RD Navbar Dropdown
  .rd-navbar-dropdown {
    display: block;
  }

  .rd-navbar-collapse-items {
    position: absolute;
    width: $rd-navbar-nav-min-width - 10px;
    padding: 25px 15px;
    box-shadow: $rd-navbar-shadow;
    color: $rd-navbar-panel-color;
    background: $rd-navbar-panel-background;
    font-size: 16px;
    line-height: 34px;

    li > * {
      vertical-align: middle;
    }

    li + li {
      margin-top: 10px;
    }

    .icon,
    a {
      display: inline-block;
      font-size: 16px;
      line-height: 30px;
    }

    .icon, a[class*="fa"]:before {
      display: inline-block;
      width: 30px;
      height: 30px;
      padding-right: 5px;
    }
  }
}

.rd-navbar-fixed .rd-navbar-nav,
.rd-navbar-sidebar {
  width: $rd-navbar-nav-min-width;
  height: 100%;
  left: 0;
  top: 0;
  -webkit-overflow-scrolling: touch;
  font-size: 16px;
  line-height: 34px;
  color: $rd-navbar-color;
  background: $rd-navbar-fixed-panel-background;
  z-index: 998;
  padding: 10px 0;

  &:before,
  &:after {
    content: '';
    display: block;
    height: $rd-navbar-min-height;
  }

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-thumb {
    background: lighten($rd-navbar-background, 10%);
    border: none;
    border-radius: 0;
    opacity: .2;
  }

  &::-webkit-scrollbar-track {
    background: $rd-navbar-background;
    border: none;
    border-radius: 0;
  }
}

.rd-navbar-fixed .rd-navbar-nav,
.rd-navbar-sidebar .rd-navbar-nav {
  li {
    position: relative;

    > a {
      position: relative;
      display: block;
      text-transform: uppercase;
      font-weight: 700;
    }

    a {
      letter-spacing: 0.06em;
    }

    &.active > a,
    &.focus > a,
    > a:hover {
      color: $rd-navbar-fixed-panel-item-hover-color;
      background: $rd-navbar-fixed-panel-item-hover-background;
    }

    & + li, > img + a, > a + a, > a + ul {
      margin-top: ($rd-navbar-min-height - $rd-navbar-min-line-height) / 2;
    }
  }

    display: block;
    height: auto;
    font-size: 16px;
    line-height: 26px;
    text-align: left;

    &:before, &:after {
      display: none;
    }

    li {
      > a {
        display: block;
        font-size: 14px;
        padding: 11px 56px 11px 16px;
        color: $rd-navbar-fixed-panel-color;
      }

      &:hover,
      &.focus,
      &.active,
      &.opened {
        > a, > a:hover {
          color: $rd-navbar-fixed-panel-item-hover-color;
          background: $rd-navbar-fixed-panel-item-hover-background;
        }
        > .rd-navbar-submenu-toggle {
          &::after {
            color: $rd-navbar-fixed-submenu-toggle-focus-color;
          }
        }
      }
    }

}

.rd-navbar-fixed,
.rd-navbar-sidebar {
  // RD Navbar Search
  .rd-navbar-search {
    &-form {
      &-submit {
        @include transform(scale(.7));
      }
    }
  }
}

@import "rd-navbar_includes/_rd-navbar-static.scss";
@import "rd-navbar_includes/_rd-navbar-fullwidth.scss";
@import "rd-navbar_includes/_rd-navbar-fixed.scss";
@import "rd-navbar_includes/_rd-navbar-sidebar.scss";
@import "rd-navbar_includes/_rd-navbar-default.scss";
@import "rd-navbar_includes/_rd-navbar-hamburger-menu.scss";
@import "rd-navbar_includes/_rd-navbar-minimal.scss";

/*
* @subsection   Page boxed layout style redeclaration
*
* @description  Redefines navbar style inside boxed layout
*
* @see          ../modules/_page-layouts.scss
*/
html.boxed {
  &.rd-navbar--has-sidebar {
    body {
      padding-left: $rd-navbar-nav-min-width + 30px;
      padding-right: 30px;
    }
  }

  .rd-navbar--is-clone {
    max-width: $layout-boxed-width;
    margin-left: auto;
    margin-right: auto;
  }
}
