/*
*
* Page Loaders
* --------------------------------------------------
*/

// Base styles
// -------------------------

.page-loader {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 9999999;
  @include display-flex;
  @include align-items(center);
  @include justify-content(center);
  padding: 20px;
  background: $white;
  transition: .3s all ease;

  &.loaded {
    opacity: 0;
    visibility: hidden;
  }

  &.ending {
    display: none;
  }

  .page-loader-body {
    text-align: center;
  }
}

// Custom Styles
// -------------------------
.cssload-container {
  width: 100%;
  height: 36px;
  text-align: center;
}

.cssload-speeding-wheel {
  width: 36px;
  height: 36px;
  margin: 0 auto;
  border: 3px solid $brand-primary;
  border-radius: 50%;
  border-left-color: transparent;
  border-bottom-color: transparent;
  animation: cssload-spin .88s infinite linear;
}

@include keyframes (cssload-spin) {
  100% {
    transform: rotate(360deg);
  }
}