/* 
 *  Owl Carousel - Animate Plugin
 */
.owl-carousel .animated {
  -webkit-animation-duration: 1000ms;
  animation-duration: 1000ms;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}
.owl-carousel .owl-animated-in {
  z-index: 0;
}
.owl-carousel .owl-animated-out {
  z-index: 1;
}
.owl-carousel .fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;
}

@-webkit-keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

/* 
 * 	Owl Carousel - Auto Height Plugin
 */
.owl-height {
  -webkit-transition: height 500ms ease-in-out;
  -moz-transition: height 500ms ease-in-out;
  -ms-transition: height 500ms ease-in-out;
  -o-transition: height 500ms ease-in-out;
  transition: height 500ms ease-in-out;
}

/* 
 *  Core Owl Carousel CSS File
 */
.owl-carousel {
  display: none;
  width: 100%;
  -webkit-tap-highlight-color: transparent;
  /* position relative and z-index fix webkit rendering fonts issue */
  position: relative;
  z-index: 1;
}
.owl-carousel .owl-stage {
  position: relative;
  -ms-touch-action: pan-Y;
}
.owl-carousel .owl-stage:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}
.owl-carousel .owl-stage-outer {
  position: relative;
  overflow: hidden;
  /* fix for flashing background */
  -webkit-transform: translate3d(0px, 0px, 0px);
}
.owl-carousel .owl-controls .owl-nav .owl-prev,
.owl-carousel .owl-controls .owl-nav .owl-next,
.owl-carousel .owl-controls .owl-dot {
  cursor: pointer;
  cursor: hand;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.owl-carousel.owl-loaded {
  display: block;
}
.owl-carousel.owl-loading {
  opacity: 0;
  display: block;
}
.owl-carousel.owl-hidden {
  opacity: 0;
}
.owl-carousel .owl-refresh .owl-item {
  display: none;
}
.owl-carousel .owl-item {
  position: relative;
  min-height: 1px;
  float: left;
  -webkit-backface-visibility: hidden;
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}


.owl-carousel.owl-text-select-on .owl-item {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.owl-carousel .owl-grab {
  cursor: move;
  cursor: -webkit-grab;
  cursor: grab;
}
.owl-carousel.owl-rtl {
  direction: rtl;
}
.owl-carousel.owl-rtl .owl-item {
  float: right;
}

/* No Js */
.no-js .owl-carousel {
  display: block;
}

/* 
 * 	Owl Carousel - Lazy Load Plugin
 */
.owl-carousel .owl-item .owl-lazy {
  opacity: 0;
  -webkit-transition: opacity 400ms ease;
  -moz-transition: opacity 400ms ease;
  -ms-transition: opacity 400ms ease;
  -o-transition: opacity 400ms ease;
  transition: opacity 400ms ease;
}

/* 
 * 	Owl Carousel - Video Plugin
 */
.owl-carousel .owl-video-wrapper {
  position: relative;
  height: 100%;
  background: #000;
}
.owl-carousel .owl-video-play-icon {
  position: absolute;
  height: 80px;
  width: 80px;
  left: 50%;
  top: 50%;
  margin-left: -40px;
  margin-top: -40px;
  font: 400 40px/80px 'FontAwesome';
  cursor: pointer;
  z-index: 1;
  -webkit-backface-visibility: hidden;
  -webkit-transition: scale 100ms ease;
  -moz-transition: scale 100ms ease;
  -ms-transition: scale 100ms ease;
  -o-transition: scale 100ms ease;
  transition: scale 100ms ease;
  &:before{
    content: '\f144';
  }
}
.owl-carousel .owl-video-play-icon:hover {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}
.owl-carousel .owl-video-playing .owl-video-tn,
.owl-carousel .owl-video-playing .owl-video-play-icon {
  display: none;
}
.owl-carousel .owl-video-tn {
  opacity: 0;
  height: 100%;
  background-position: center center;
  background-repeat: no-repeat;
  -webkit-background-size: contain;
  -moz-background-size: contain;
  -o-background-size: contain;
  background-size: contain;
  -webkit-transition: opacity 400ms ease;
  -moz-transition: opacity 400ms ease;
  -ms-transition: opacity 400ms ease;
  -o-transition: opacity 400ms ease;
  transition: opacity 400ms ease;
}
.owl-carousel .owl-video-frame {
  position: relative;
  z-index: 1;
}

/*
 * Owl Navigation
 */

.owl-nav{
  &.disabled {
    display: none;
  }
}

.owl-prev, .owl-next{
  position: absolute;
  top: 50%;
  @include transform(translateY(-50%));
  font: 400 46px/50px 'FontAwesome';
  opacity: .5;
  cursor: pointer;

  &:hover{
    opacity: 1;
  }
}

.owl-prev{
  left: 0;
  &:before{
    content: '\f104';
  }
}

.owl-next{
  right: 0;
  &:before{
    content: '\f105';
  }
}

/*
 * Owl Pagination
 */

.owl-dots{
  text-align: center;
  margin-top: 10px;

  &.disabled {
    display: none;
  }
}

.owl-dot {
  display: inline-block;
  text-align: center;
  border-radius: 50%;
  position: relative;
  margin-left: 10px;
  width: 16px;
  height: 16px;
  outline: none;
  cursor: pointer;
  background-color: transparent;
  box-shadow: inset 0 0 0 2px $brand-primary;
  transition: box-shadow 0.3s ease;

  &:hover,
  &:focus {
    box-shadow: inset 0 0 0 2px rgba($brand-primary, 0.6);
  }

  &.active {
    box-shadow: inset 0 0 0 8px $brand-primary;
  }
}

.display {
  transform: scale(0);
}

.display,
.color {
  transition: 3s;
}

.owl-item {
  transition: .3s;
}


// owl in tabs
//----------------------------------------

.owl-tab-centered {
  padding: 12px 0;
  min-height: 600px;

  .owl-stage {
    min-height: 600px;
  }

  .owl-nav {
    display: none;
  }

  .owl-prev,
  .owl-next {
    &:before {
      display: inline-block;
      height: 45px;
      width: 45px;
      border-radius: 50%;
      border: 2px solid;
      font-family: "Material Design Icons";
      font-size: 26px;
      line-height: 42px;
      color: $gray;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      transition: 250ms;
      text-align: center;

    }

    &:hover {
      &:before {
        color: $white;
        background: $brand-primary;
        border-color: $brand-primary;
      }
    }
  }

  .owl-prev {
    left: 5px;

    &:before {
      content: "\f400";
    }
  }

  .owl-next {
    right: 5px;

    &:before {
      content: "\f400";
      transform: rotate(180deg);
    }
  }

  @media (min-width: $screen-md) {
    .owl-nav { display: block; }
    .owl-dots { display: none; }
  }

  @media (min-width: $screen-lg-min) {
    &:before {
      position: absolute;
      top: -3px;
      bottom: 0;
      right: 0;
      left: 0;
      content: "";
      display: inline-block;
      background: url("../images/slick-center-bg.png") no-repeat center center;
      z-index: 1;
      pointer-events: none;
    }
  }


  .slick-track {
    @include display-flex;
    @include align-items(stretch);
    min-height: 575px;

    @media (min-width: $screen-sm-min) {
      min-height: 600px;
    }
  }

  .slick-slide {
    height: auto;

    @include display-flex;
    @include align-items(center);
  }

  @media (min-width: 1200px) {
    .owl-prev {
      margin-left: -255px;
      left: 50%;
    }

    .owl-next {
      margin-right: -255px;
      right: 50%;
    }
  }

  .thumbnail-menu-modern {
    min-height: 600px;
    background-color: transparent;
    box-shadow: none;
    border-radius: 0;
    margin-left: auto;
    margin-right: auto;

    @media (min-width: $screen-lg-min) {
      padding: 132px 65px 130px;
    }

    figure{
      width: auto;
    }

    .caption {
      max-height: none;
      margin-top: 6px;

      h5 + p {
        margin-top: 10px;
      }
    }

    .text-italic,
    .btn{
      opacity: 1;
      visibility: visible;
    }

    @media (min-width: $screen-lg-min) {
      .price{
        margin-top: -10px;
      }

      .btn,
      .price{
        transition: 450ms;
      }
      .text-italic{
        transition: 250ms ease-out;
      }
      .text-italic,
      .btn{
        font-size: 0;
        opacity: 0;
        visibility: hidden;
        @include transform(scale(0));
      }
    }
  }

  .center {
    @media (min-width: $screen-md-min) {
      .thumbnail-menu-modern {
        &:before {
          position: absolute;
          top: 0;
          bottom: 0;
          right: 0;
          left: 0;
          content: "";
          display: inline-block;
          border: 4px solid #e0e1e9;
        }
      }
    }

    .thumbnail-menu-modern {
      @media (min-width: $screen-lg-min) {
        padding: 42px 35px 50px;
        &:before{
          content: none;
        }
      }
    }
    .text-italic,
    .btn{
      font-size: 14px;
      opacity: 1;
      visibility: visible;
      @include transform(scale(1));
    }
    .price{
      margin-top: 3px;
    }
  }
}