/*
* Sidebar Layout
*/
html.rd-navbar-sidebar-linked {
  body {
    padding-left: 320px;
    .rd-navbar-wrap{
      position: fixed;
    }
    .page{
      box-shadow: $shadow-area-xs;
    }
  }
}

.rd-navbar-sidebar {
  position: fixed;
  display: block;
  overflow: auto;
  width: 320px;
  box-shadow: $shadow-area-xs;

  address {
    line-height: 20px;
  }

  span.rd-navbar-submenu-toggle {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
  }

  .rd-navbar-inner {
    text-align: left;

    padding-left: 30px;
    padding-right: 30px;
  }

  .rd-navbar-nav {

    @media (min-width: $screen-lg-min) {
      margin-top: 30px;
    }
    li > a + ul{
      margin-top: 0;
    }
    > li {
      border-top: 1px solid #e5e7e9;

      &:last-of-type {
        border-bottom: 1px solid #e5e7e9;
      }
      & + li {
        margin-top: 0;
      }
    }

    li {

      &:hover,
      &.focus,
      &.active{

        > a, > a:hover {
          color: $burnt-sienna;
          background: transparent;
        }
        > .rd-navbar-submenu-toggle {
           &:after{
             color: $burnt-sienna;
           }
          &:hover {
            cursor: pointer;
            color: $burnt-sienna;
          }
        }
      }

      .rd-navbar-dropdown,
      .rd-navbar-megamenu {
        transition: opacity 0.3s, height 0.4s ease;
        opacity: 0;
        height: 0;
        overflow: hidden;
        padding-left: 17px;

         li > a {
          padding-top: 0;
           font-weight: 400;
        }

         li > a:before {
          position: absolute;
          top: 5px;
          left: 0;
          content: '\f105';
          font: 400 15px 'FontAwesome';
          color: $burnt-sienna;
          opacity: 1;
          transition: .33s all ease;
          transition-delay: .1s;
        }
         li:hover {
          > a {
            padding-left: 16px;
          }

          > a:before {
            left: 4px;
          }
        }
        .menu-img {
           img{
             display: none;
           }
        }
      }

      &.opened {
        > .rd-navbar-dropdown,
        > .rd-navbar-megamenu {
          opacity: 1;
          height: auto;
        }
        > a {
          color: $burnt-sienna;
          background: transparent;
        }
        > .rd-navbar-submenu-toggle {

          &::after {
            color: $burnt-sienna;
            @include transform (rotate(180deg));
          }
        }
      }
    }
  }

  .rd-navbar-default & {
    .rd-navbar-aside-right .list-links.list-inline > li {
      margin-top: 32px;
    }
  }


  .rd-navbar-dropdown,
  .rd-navbar-megamenu {
    
  }

  .rd-navbar-megamenu {
    > li {
      padding-top: 15px;

      &:last-child {
        padding-bottom: 10px;
      }
    }

    .rd-megamenu-header {
      padding-left: 15px;
      padding-right: 15px;
      color: $brand-primary;
      font-size: 16px;
    }

    .list-marked {
      margin-top: 10px;
      > li {
        padding-left: 0;
        &:before {
          display: none;
        }
      }
    }

    // Offsets
    * + .rd-megamenu-header {
      margin-top: 15px;
    }
  }

  // RD Navbar Toggle
  .rd-navbar-submenu-toggle {
    &::after {
      content: '\f107';
      position: absolute;
      top: 22px;
      right: 0;
      margin-top: -22px;
      width: 65px;
      height: 44px;
      font: 400 14px "FontAwesome";
      line-height: 42px;
      text-align: center;
      transition: 0.4s all ease;
      z-index: 2;
    }
  }

  // RD Navbar Collapse
  .rd-navbar-collapse-items {
    top: 0;
    left: 0;
    padding-top: 45px;
    @include transform(scale(.7));
    @include transform-origin(0% 0%);
    opacity: 0;
    visibility: hidden;
  }

  .rd-navbar-collapse {
    position: absolute;
    top: 4px;
    left: 4px;
    display: inline-block;
    z-index: 1;

    &.active {
      .rd-navbar-collapse-items {
        opacity: 1;
        visibility: visible;
        @include transform(scale(1));
      }
    }
  }

  .rd-navbar-shop {
    padding-left: 15px;
    margin-top: 10px;
  }
  .rd-navbar-brand .brand-logo {
    svg{
      width: 198px;
      height: 55px;
    }
  }
}