//
// Offsets
// --------------------------------------------------

// Insets
// -------------------------

$insets: (0, 10px, 15px, 20px, 30px, 45px, 50px, 70px, 80px);

@include indent-responsive(inset, left, padding-left, $medias, $insets);
@include indent-responsive(inset, right, padding-right, $medias, $insets);

// Elements
// -------------------------
.page {
	h1 + *,
	h2 + *,
	h3 + *,
	h4 + *,
	h5 + *,
	h6 + * {
		margin-top: 20px;
		@media (min-width: $screen-md-min) {
			margin-top: 40px;
		}
	}

	* + p {
		margin-top: 12px;
	}

	h2 + p {
		margin-top: 15px;
	}
	h3 + p,
	h4 + p,
	h5 + p,
	h6 + p {
		margin-top: 20px;
	}
}

.bg-white {
	& + .bg-white {
		padding-top: 0;
	}
}

// Grid
// -------------------------
.row-no-gutter {
	margin-left: 0;
	margin-right: 0;
}

// Sections
// -------------------------

// Responsive
// -------------------------

$offsets: (0, 5px, 10px, 15px, 20px, 25px, 30px, 35px, 40px, 45px, 50px, 60px, 65px, 70px, 80px, 85px, 90px, 100px);

html .page {
	@include indent-responsive(offset, top, margin-top, $medias, $offsets);
}

// Range spacing
html {
	.range-0 { @include grid-offset(0px); }
	.range-15 { @include grid-offset(15px); }
	.range-20 { @include grid-offset(20px); }
	.range-30 { @include grid-offset(30px); }
	.range-50 { @include grid-offset(50px); }

	@media (min-width: $screen-md) {
		.range-md-30 { @include grid-offset(30px); }
	}
}