/*
* Navbar components
*/

// RD Navbar
.rd-navbar {
  display: none;
  background: $rd-navbar-background;
  box-shadow: $rd-navbar-shadow;
}

// RD Navbar Toggle
.rd-navbar-toggle {
  @include make-toggle(
          $rd-navbar-toggle-preset,
          $rd-navbar-min-line-height,
          $rd-navbar-min-font-size,
          $rd-navbar-fixed-toggle-color
  );
  background-color: transparent;
  border: none;
  &:focus{
    outline: none;
  }
  display: none;
}

// RD Navbar Brand
.rd-navbar-brand {
  display: inline-block;
  .brand-name {
    color: $gray-dark;
    font-size: 25px;
    line-height: 40px;
    font-weight: 700;
  }

  .brand-slogan {
  }
}

.brand-inverse {
  svg text{
    fill: $white ;
  }
}
// RD Navbar Nav
.rd-navbar-nav {
}

// RD Navbar Dropdown
.rd-navbar-dropdown {
  display: none;
}

.rd-navbar {
  // RD Navbar Search
  .rd-search {
    width: 350px;
    &-toggle {
      display: inline-block;
      font: 400 18px/36px "FontAwesome";

      &, &:before, &:after {
        text-align: center;
        width: 36px;
        height: 36px;
      }

      &:before, &:after {
        position: absolute;
        left: 0;
        top: 0;
      }

      &:before {
        content: '\f002';
        transition: .3s all ease;
        @include transform(scale(1) rotate(0deg));
      }

      .rd-navbar:not(.rd-navbar-fixed) & {
        &:after {
          content: '\f00d';
          transition: .3s all ease;
          @include transform(scale(0) rotate(-90deg));
        }

        &.active {
          &:before {
            @include transform(scale(0) rotate(90deg));
          }

          &:after {
            @include transform(scale(1) rotate(0deg));
          }
        }
      }

      .rd-navbar-fixed &, .rd-navbar-fixed &:before {
        width: 48px;
        line-height: 48px;
      }
    }

    &-form-submit {
      @include make-toggle(
              $rd-navbar-search-toggle-preset,
              $rd-navbar-min-line-height,
              $rd-navbar-min-font-size,
              $rd-navbar-panel-color
      );
    }
  }
}
// RD Navbar Live Search Results
.rd-navbar-live-search-results {
  position: absolute;
  left: 4px;
  right: 4px;
  padding: 16px 8px 8px;
  top: 100%;
  font-size: 16px;
  line-height: 34px;
  color: #333;
  background: $white;
  box-shadow: $rd-navbar-shadow;
  opacity: 0;
  visibility: hidden;
  text-align: left;
  z-index: 998;

  .search-quick-result {
    padding-left: 8px;
    font-size: 14px;
    line-height: 30px;
    color: #757575;
  }

  .search_list {
    margin-top: 4px;
    font-size: 16px;
    line-height: 30px;

    li + li {
      margin-top: 2px;
    }

    .search_list li + li:last-child {
      margin-top: 8px;
      border-top: 1px solid #EBEBEB;
      padding-top: 7px;
    }
  }

  .search_link {
    display: block;
    padding: 8px;
    color: #757575;
    border-radius: 2px;

    &:hover {
      background: #F7F7F7;
    }

    p {
      margin-top: 0;
      font-size: 14px;
      display: none;
    }
  }

  .search_title {
    color: #212121;
    font-weight: 400;
  }

  .search_submit {
    display: block;
    text-align: center;
    padding: 8px;
    font-weight: 700;
    color: $brand-primary;
    text-transform: uppercase;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;

    &:hover {
      background: #F7F7F7;
    }
  }

  &.active {
    visibility: visible;
    opacity: 1;
  }

  @media (min-width: $screen-lg-min) {
    .search_link {
      p {
        display: block;
      }
    }
  }
}

.rd-navbar-live-search-results {
  @include transform(translateY(-100%));

  &.active {
    @include transform(translateY(0));
  }
}

// Rd navbar shop

.rd-navbar-shop{
  display: inline-block;
  font-size: 22px;
  color: $white;


  .rd-navbar-static & {
    li:last-of-type {
      a{
        position: relative;
        top: 3px;
      }
    }
  }

  .rd-navbar-minimal & {
    @include transform(translateY(5%));
    @include align-self(baseline);
  }
}

.rd-navbar-nav {

  .navbar-icon {
    &-item{
      vertical-align: middle;
      padding-right: 5px;
      min-width: 45px;
      font-size: 40px;
      line-height: 20px;
      display: inline-block;
      color: $rd-navbar-nav-color;
      &:before {
        line-height: inherit;
        font-size: inherit;
      }
    }
  }
  .restaurant-icon-19 {
      font-size: 41px;
      position: relative;
      top: -1px;
  }
  .thin-icon-tag {
      font-size: 32px;
  }
  .thin-icon-map-marker {
      font-size: 35px;
  }
  .thin-icon-layers {
      font-size: 31px;
  }
}

// img in menu
//----------------------

.menu-img{
  
  .rd-navbar-fixed & {
    img,
    span {
      display: inline-block;
      vertical-align: middle;
    }
    span {
      padding-left: 15px;
    }
  }
  .rd-navbar-static & {
    span{
      display: block;
      margin-top: 5px;
    }
  }

}

.rd-navbar-fixed,
.rd-navbar-static,
.rd-navbar-fullwidth {

  .rd-navbar-nav .menu-img {
    &:hover,
    &:active,
    &.focus,
    &.opened {
      > a,
      > a:hover {
        background-color: $gray-lighter;
        color: initial;
      }
    }
  }

}
.rd-navbar-nav > li {
  .rd-navbar-dropdown.menu-img-wrap {
    .rd-navbar-static & {
      @include display-flex;
      @include flex-wrap(wrap);
      min-width: 354px;
      text-align: center;
      padding: 0;

      li {
        @include display-flex;
        @include align-items(flex-end);
        @include flex-basis(33.3344%);
        min-width: 33.3344%;
        padding: 0;
        border-bottom: 0;

        > a {
          padding: 15px 13px;
          &:before{
            content: none;
          }
        }

        & + li {
          margin-top: 0;
          border-left: 1px solid $gray-lighter;
        }

        &:nth-child(n+4) {
          border-top: 1px solid $gray-lighter;
        }
      }
    }
  }
}

