//
// RD Navbar Hamburger
// --------------------------------------------------
.rd-navbar-hamburger-menu {

  // Mobile version
  .rd-navbar-fixed {
  }

  // Desktop version
  .rd-navbar-static {
    .rd-navbar-inner {
      padding-right: 109px;
    }

    .rd-navbar-toggle {
      @include make-toggle(
                      $rd-navbar-toggle-preset,
                      $rd-navbar-min-line-height,
                      $rd-navbar-min-font-size,
                      $brand-primary
      );
      display: block;
      position: absolute;
      top: 50%;
      @include transform(translateY(-50%));
      right: 0;
    }

    .rd-navbar-nav-wrap {
      .rd-navbar-nav,
      & + .rd-navbar-shop {
        opacity: 0;
        visibility: hidden;
        transition: .33s all ease;
      }

      &.active .rd-navbar-nav,
      &.active + .rd-navbar-shop {
        opacity: 1;
        visibility: visible;
      }

      &.active .rd-navbar-search {
        opacity: 0;
        visibility: hidden;
      }
    }

    .rd-navbar-search {
      margin-right: -75px;
      opacity: 1;
      visibility: visible;
      transition: .33s all ease;
    }

    .rd-navbar-shop {
      position: absolute;
      right: 57px;
      top: 52%;
      @include transform(translateY(-50%));
    }

    @media (min-width: $screen-lg-min) {
      .rd-navbar-nav-wrap {
        padding-right: 0;
      }

      .rd-navbar-shop {
        right: 55px;
      }

      .rd-navbar-search {
        margin-right: -50px;
      }
    }

    @media (min-width: $screen-xl-min) {
      .rd-navbar-nav {
        margin-right: 50px;
      }

      .rd-navbar-toggle {
        right: -14px;
      }

      .rd-navbar-shop {
        right: 48px;
      }

      .rd-navbar-search {
        margin-right: -72px;
      }
    }

    // States
    &.rd-navbar--is-clone,
    &.rd-navbar--is-stuck {
    }
  }

  // Styles
  &.rd-navbar-hamburger-inverse .rd-navbar-static:not(.rd-navbar--is-stuck) {
    // RD Navbar Brand
    .rd-navbar-brand {
      .brand-logo svg {
        fill: $white;
      }

      .brand-name,
      .brand-slogan {
        &,
        &:active,
        &:focus,
        &:hover {
          color: $white;
        }
      }
    }

    // RD Navbar Toggle
    .rd-navbar-toggle {
      span {

        &,
        &:before,
        &:after {
          background: $white;
        }

        &:hover {
          &,
          &:before,
          &:after {
            background: $white;
          }
        }
      }

      &.active {
        span {
          background-color: transparent;
        }
      }
    }

    // RD Navbar Nav
    .rd-navbar-nav {
      > li {
        > a {
          &,
          &:active,
          &:focus {
            color: $white;
          }
        }

        &.focus > a,
        &.active > a,
        &.opened > a,
        > a:hover {
          color: $brand-primary;
        }

        > .rd-navbar-submenu-toggle:after {
          color: $white;
        }

        &.focus,
        &.opened,
        &.active {
          > .rd-navbar-submenu-toggle:after {
            color: $brand-primary;
          }
        }
      }
    }

    // RD Navbar Search
    .rd-navbar-search:not(.active) {
      .rd-navbar-search-toggle {
        &,
        &:active,
        &:focus {
          color: $white;
        }

        &:hover {
          color: $brand-primary;
        }
      }
    }
  }
}
