//
// RD Navbar Default
// --------------------------------------------------
.rd-navbar-default {
  .desktop &{
    @media (min-width: $screen-md-min) {
      margin-bottom: -30px;
    }
  }

  .rd-navbar-brand svg {
    width: 138px;
  }

  // Mobile version
  .rd-navbar-fixed,
  .rd-navbar-sidebar{
    .rd-navbar-nav-wrap-outer .rd-navbar-aside-right {
      display: none;
    }

    .rd-navbar-shop{
      padding-top: 10px;
      .label-inline {
        vertical-align: baseline;
      }
      .icon{
        padding: 0 2px 0 7px;
      }
    }

    .rd-navbar-dropdown {
      img{
        padding: 10px;
      }
    }

    .rd-navbar-submenu-toggle::after {
      top: 25px;
    }

    .rd-navbar-aside-right {
      padding-left: 15px;
      padding-right: 15px;

      .list-links {
        > li{
          margin-top: 28px;
        }
      }
    }
  }

  // Desktop version
  .rd-navbar-static {
    padding-top: $top-panel-height;

    .rd-navbar-nav-wrap {
      position: relative;
      background-color: $brand-primary-lighter;
      padding: 12px 32px 13px 53px;
      border-radius: $border-radius-xl;
      height: 64px;

      .rd-navbar-aside-right {
        display: none;
      }
    }

    .rd-navbar-inner {
      padding: 0;
    }

    .rd-navbar-nav {
      > li {
        @media (max-width: 1199px){
          padding-left: 12px;
          padding-right: 12px;
        }
        &.focus > a,
        > a:hover {
          color: $white;
        }

        &.active > a {
          color: $white;
        }

        &.focus > .rd-navbar-submenu-toggle,
        &.opened > .rd-navbar-submenu-toggle,
        & > a:hover + .rd-navbar-submenu-toggle {
          &:after{
            color: $white;
          }
        }

        .rd-navbar-dropdown {
          margin-top: 21px;
        }
        .rd-navbar-megamenu {
          max-width: 940px;
          left: 50%;
          margin-left: -470px;
          margin-top: 0;
        }
        @media (min-width: $screen-lg-min) {
          .rd-navbar-dropdown {
            margin-top: 19px;
          }
          .rd-navbar-megamenu {
            max-width: 1080px;
            left: 50%;
            margin-left: -540px;
          }
        }
      }
      > li + li {
        border-left: 0;
      }
    }

    .rd-navbar-shop {
      @include order(2);
      @include transform(translateY(2%));

      span:first-of-type{
        display: none;
      }
      .link-shop:hover{
        color: $white;
      }
    }

    .rd-navbar-nav-wrap-outer {
      position: absolute;
      top: -$top-panel-height;
      left: 0;
      right: 0;
      z-index: 1;
    }

    .rd-navbar-aside {
      @include display-flex;
      @include flex-direction(row);
      @include flex-wrap(nowrap);
      @include align-items(center);
      @include justify-content(center);
      height: $top-panel-height;

      &-content {
        width: 100%;
      }
    }
    .rd-navbar-megamenu .list-marked {
      columns: 2;
      -moz-columns: 2;
      width: 100%;
      column-gap: 10px;

      > li {
        -webkit-column-break-inside: avoid;
        page-break-inside: avoid;
        break-inside: avoid;
      }
    }

    .rd-navbar-megamenu {
      > li:last-of-type {
        width: 25%;
        .list-marked {
          columns: 1;
          -moz-columns: 1;
        }
      }
    }

    .list-links {
      > li {
        vertical-align: middle;

        @media (min-width: $screen-md-min) {
          & + li {
            margin-left: 20px;
          }
        }
        @media (min-width: $screen-lg-min) {
          & + li {
            margin-left: 50px;
          }
        }

      }
    }

    // States
    &.rd-navbar--is-clone,
    &.rd-navbar--is-stuck {
      padding-top: 0;
      background: $brand-primary-lighter;

      .rd-navbar-megamenu {
        top: 62px;
      }
      .rd-navbar-inner .rd-navbar-nav .rd-navbar-dropdown {
        margin-top: 19px;
      }
    }
  }
}
