/*
* Static Layout
*/

.rd-navbar-static {
  position: relative;
  display: block;
  transition: none;

  .rd-navbar-inner {
    @include display-flex;
    @include flex-wrap(nowrap);
    @include justify-content(space-between);
    @include align-items(center);
    padding: 20px 0;
    text-align: left;
    font-size: 0;
    max-width: 1200px;

    @media (min-width: $screen-lg-min) {
      padding: 29px 0;
      text-align: left;
    }

    &.active {
      .rd-navbar-panel,
      .rd-navbar-nav,
      .rd-navbar-shop {
        opacity: 0;
        visibility: hidden;
      }
    }
  }

  // RD Navbar brand
  .rd-navbar-brand {
    position: relative;
  }

  // RD Navbar nav wrap
  .rd-navbar-nav-wrap {
    @include display-flex;
    @include flex-wrap(nowrap);
    @include align-items(inherit);
    @include justify-content(space-between);
    @include flex-grow(1);
    text-align: center;
    font-size: 0;
    height: 50px;
  }

  .rd-navbar-panel {
    min-width: 190px;
  }

  // RD Navbar Nav
  .rd-navbar-nav {
    z-index: 0;

    > li {
      > a {
        font-size: 12px;
      }
    }

    > li {
      padding-left: 5px;
      padding-right: 5px;
    }

    @media (min-width: $screen-lg-min) {
      margin-right: 30px;
      > li > a {
        font-size: 14px;
      }
      > li {
        padding-left: 20px;
        padding-right: 20px;

        & + li {
          border-left: 1px solid $gray;
        }
      }
    }

    @media (min-width: 1600px) {
      margin-right: 50px;
    }
  }

  .ie-11 &,
  .ie-10 & {
    .rd-navbar-nav {
      margin-right: 20px;
    }
  }

  // RD Navbar Search
  .rd-navbar-search {
    z-index: 12;

    .rd-search {
      @include display-flex;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      opacity: 0;
      visibility: hidden;
      @extend %rd-navbar-transition;

      background: $white;

      &:before {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        width: 100vw;
        left: 50%;
        @include transform(translateX(-50%));
        background: $white;
      }
    }

    .form-group {
      @include display-flex;
      @include flex-direction(column);
      @include align-items(stretch);
      width: 100%;
    }

    .form-control,
    .form-label {
      font-family: $font-family-sec;
      font-size: 26px;
      font-weight: 700;
      color: $gray-darker;
    }

    .form-control {
      @include flex-grow(1);
      width: 100%;
      height: 100%;
      padding: 0 139px 0 15px;
      border: 0;

      &::-ms-clear {
        display: none;
      }
    }

    .form-label {
      top: 50%;
      left: 15px;

      &.focus {
        top: 0;
        @include transform-origin(0 50%);
        @include scale(.6);
        opacity: .8;
      }
    }

    @media (min-width: $screen-lg-min) {
      .form-control,
      .form-label {
        font-size: 38px;
      }

      .form-label {
        left: 30px;
        &.focus {
          top: 4px;
        }
      }

      .form-control {
        padding-left: 30px;
      }
    }

    @media (min-width: $screen-xl-min) {
      .form-control,
      .form-label {
        font-size: 38px;
      }

      .form-label {
        left: 0;
      }

      .form-control {
        padding-left: 0;
        padding-right: 160px;
      }
    }

    .rd-navbar-search-toggle {

    }

    .rd-search-submit {
      display: none;
    }

    &.active {
      .rd-search,
      .rd-navbar-search-toggle {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  // RD Navbar aside
  .rd-navbar-aside {
    @include order(1);
    position: relative;
    z-index: 0;

    max-width: 100%;
    padding: 12px 0 13px;

    &-content {
      @include display-flex;
      @include flex-direction(row);
      @include flex-wrap(nowrap);
      @include align-items(center);
      @include justify-content(space-between);
      font-size: 0;
    }

    @media (max-width: $screen-md-max) {
      .link-sm {
        font-size: 12px;
      }

      .icon-xs,
      .icon-sm {
        @include icon-sizing(18px);
      }
    }

    @media (min-width: $screen-lg-min) and (max-width: 1599px) {
      .link-sm {
        font-size: 14px;
      }
    }

    @media (min-width: $screen-xl-min) {
      padding-left: 0;
      padding-right: 0;
    }
  }

  // RD Navbar clone
  &.rd-navbar--is-clone {
    display: block;
    @include transform(translateY(-105%));
    transition: .33s all ease;

    &.rd-navbar--is-stuck {
      @include transform(translateY(0));
    }
  }

  // States
  &.rd-navbar--is-clone,
  &.rd-navbar--is-stuck {
    box-shadow: $shadow-area-xs;
    .rd-navbar-nav-wrap {
      margin-top: 0;
    }

    .rd-search {
      top: 0;
    }

    .form-label.focus {
      top: 3%;
    }

    .form-control,
    .form-label {
      font-size: 20px;
    }

    .rd-navbar-nav-wrap-outer {
      display: none;
    }

    .rd-navbar-inner {
      padding: 15px 0;

      .rd-navbar-nav{
        .rd-navbar-dropdown{
          margin-top: 15px;
        }
      }
    }
  }
}