/*
* Material Parallax
*/
.rd-parallax{

  &-inner{
    position: relative;
    overflow: hidden;
    -webkit-transform: translate3d(0px, 0px, 0px);
    transform: translate3d(0px, 0px, 0px);
    clip: rect(0,auto,auto,0);
  }

  &-layer[data-type="media"]{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100%;

    iframe{
      width: 100%;
      height: 100%;
    }
  }

  &-layer[data-url]{
    -webkit-background-size: cover;
    background-size: cover;
    background-position: center center;
    pointer-events: none;
  }
  &-layer-no-size{
    .rd-parallax-layer[data-url] {
        background-position: 0;
        @media (min-width: $screen-sm-min) {
          background-position: center center;
          background-size: 100%;
          background-repeat: no-repeat;
          pointer-events: none;
      }
    }
  }

  // Styles for Parallax with slider
  &-swiper{
    z-index: 1!important;
  }
  &-light {
    * {
      color: $white;
    }
  }

  .form-control {
    background: rgba($white,.2);
    border-color: rgba($white,.2);
    @include placeholder($white);
  }
  .form-validation {
    color: rgb(249, 181, 181);
  }
}



.parallax-content {
  position: relative;
  z-index: 1;
}

.parallax-container {
  position: relative;
  overflow: hidden;
  background-position: center center;
  background-size: cover;
}

.material-parallax {
  position: absolute;
  top: 0;
  left: -1px;
  right: -1px;
  bottom: 0;
  z-index: 0;
  background-position: center center;
}

.ipad,
.iphone {
  .parallax-container {
    background-attachment: scroll !important;
  }
}

.material-parallax img {
  display: none;
  position: absolute;
  left: 50%;
  bottom: 0;
  min-width: 101%;
  min-height: 101%;
  max-width: none;
  transform: translate3d(-50%, 0, 0);
}
