/*
* @subsection   Isotope
*/

$isotope-masonry-item-offset: 30px;

[data-isotope-layout] {
  position: relative;
  @include transform(translateY(-$isotope-masonry-item-offset));
  display: block;
  margin-bottom: -$isotope-masonry-item-offset;
  transition: .4s all ease;
  min-height: 160px;

  &:after {
    content: '';
    position: absolute;
    margin-top: $isotope-masonry-item-offset / 2;
    width: 64px;
    height: 64px;
    top: 50%;
    left: 50%;
    background-image: url('data:image/png;base64,');
    background-position: -1152px 0;
    animation: 0.7s sprite-animation steps(18) infinite;
    transition: .4s all ease;
    @include transform(translate(-50%, -50%));
  }

  [class*="col-"] {
    display: block;
    margin-top: $isotope-masonry-item-offset;
    opacity: 0;
    transition: .4s opacity ease;
    will-change: opacity;
    /*
     * @bugfix item position after filtering
     * @affected iOS
    */
    //width: 100%;
  }

  &.isotope--loaded {
    [class*="col-"] {
      opacity: 1;
    }

    &:after {
      opacity: 0;
      visibility: hidden;
    }
  }
}


.isotope.isotope-condensed {
  @include transform(none);
  margin-bottom: 0;
}

.isotope-filters {
  $value: 10px;

  > * {
    margin-top: 0;
    display: inline-block;
    vertical-align: middle;
  }

  .inline-list {
    position: relative;
    @include transform(translateY(-$value));
    margin-bottom: -$value;

    li {
      margin-top: $value;
      display: inline-block;

      @media (min-width: $screen-md-min) {
        & + li {
          margin-left: 40px;
        }
      }
    }

    a {
      font-size: 18px;
      font-weight: 700;
      position: relative;
      color: $gray-dark;
      cursor: pointer;
      transition: .3s;

      &:before{
        position: absolute;
        top: 120%;
        left: 0;
        right: 100%;
        content: "";
        display: inline-block;
        opacity: 0;
        transition:350ms;
        border-top: 2px solid $brand-primary;
      }

      &:hover,
      &.active {
        &:before{
          right: 0;
          opacity: 1;
        }
      }
    }
  }

  @media (min-width: $screen-md-min) {
    .isotope-filters-trigger {
      display: none;
    }
  }
}

.isotope-filters-buttons {
  word-spacing: 0;
  .inline-list {
    > li:not(:last-child) {
      margin-right: 10px;
    }

    a {
      display: inline-block;
      position: relative;
      font-size: 14px;
      font-weight: 700;
      text-transform: uppercase;
      color: $text-color;
      border: 2px solid #e0e1e9;

      &:before{
        content: none;
      }
      &:focus{
        outline: 0;
      }

      &:hover,
      &.active {
          color: $burnt-sienna;
          outline: 0;
      }
    }
    @media (min-width: $screen-md-min) {
      li + li {
        margin-left: 15px;
      }
    }
  }
  @media (min-width: $screen-md-min) {
    &.isotope-filters-buttons-primary-lighter {
      .inline-list {
        a {
          color: $white;
        }
        a:hover,
        a.active {
          color: $brand-primary-lighter;
          border-color: $brand-primary-lighter;
        }
      }
    }
  }
}

.isotope-filters-responsive {
  position: relative;
  z-index: 10;



  @media (max-width: $screen-sm-max) {
    max-width: 370px;
    margin-left: auto;
    margin-right: auto;
  }

  @media (max-width: $screen-sm-max) {
    vertical-align: middle;

    .isotope-filters-toggle {
      padding-left: 10px;
      padding-right: 10px;
      .caret {
        margin-left: 5px;
      }
    }

    > li {
      vertical-align: middle;
    }

    > li:first-child {
      margin-right: 8px;
    }

    > li + li {
      margin-top: 8px;
    }

    .isotope-filters {
      position: absolute;
      top: 100%;
      left: 0;
      z-index: 10;
      width: 100%;
      min-width: 240px;
      padding: 15px 9px;
      background: $white;
      border: 1px solid rgba($gray-darker, .1);
      border-radius: 10px;
      text-align: left;
      visibility: hidden;
      opacity: 0;
      transition: .33s all ease;

      &.active {
        visibility: visible;
        opacity: 1;
      }

      .inline-list {
        width: 100%;
      }

      .inline-list a {
        display: inline-block;
        width: 100%;
        padding: 3px 8px;
        font-size: 14px;
        border-radius: 11px;
        border: 0;
        background-color: transparent;
        &:before{
          content: none;
        }

        &.active,
        &:hover {
          color: $brand-primary;
        }
      }

      li {
        display: block;
        width: 100%;
      }

      li + li {
        margin-top: 4px;
      }
    }

    @media (min-width: $screen-xs-min) {
      > li {
        display: inline-block;
        margin: 0;
      }

      > li + li {
        margin-top: 0;
      }
    }

    @media (min-width: $screen-sm-min) {
      .isotope-filters {
        min-width: 250px;
        .inline-list a {
          padding: 5px 10px;
        }
      }
    }
  }

  @media (min-width: $screen-md-min) {
    > li:first-child {
      display: none;
    }
  }
}
.block-top-level {
  &:focus,
  &:active {
    outline: none;
  }
}
.isotope-filters-toggle {
  //display: block;
  &:focus,
  &:active {
    outline: none !important;
  }
  @media (min-width: $screen-md-min) {
    display: none;
  }
}

.isotope-col {
  width: .01%;
}

.isotope-item-width-50p {
  width: 100%;
  @media (min-width: $screen-sm-min) {
    width: 49.97%;
  }
}

.isotope-item-width-25p {
  width: 100%;
  @media (min-width: $screen-sm-min) {
    width: 49.96%;
  }
  @media (min-width: $screen-md-min) {
    width: 24.96%;
    overflow: hidden;
  }
}

.isotope-item-width-50p,
.isotope-item-width-25p {
  will-change: opacity;
  > * {
    position: relative;
    @include transform(scale3d(1.0033, 1.003, 1));
  }
}

.isotope.isotope-condensed[data-isotope-layout='masonry'] {
  margin-right: -5px;
}

// isotope btn
//--------------------------------------

.isotope-menu {
  margin-top: 30px !important;
  .isotope-item {
    @media (min-width: $screen-lg-min) {
      margin-top: -30px;
      &:hover {
        z-index: 2;
      }
    }
  }
}

.isotope-menu-offsets {
  margin-top: -15px !important;
  .isotope-item {
    margin-top: 45px;
  }
}